import MasterPage from '../components/Master';

//importando depedencias
import fetch from 'isomorphic-unfetch';

//importando componentes
import Precio from '../components/Precio'

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio={props.precioBitcoin}
                    precioAyer={props.precioBitcoinAyer}
                />
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre Bitcoin</h2>
            </div>

            <div className="col-md-4">
            <h2>Próximos Eventos Bitcoin</h2>
            </div>
        </div>
    </MasterPage> 
);

Index.getInitialProps = async () => {
    const precio = await fetch("https://api.coindesk.com/v1/bpi/currentprice.json"); //usando un api diferente porque CoinMarketCap no es fácil de trabajar y cambió desde que se hizo el curso.
    const resPrecio = await precio.json(); 

    const historicoPrecio = await fetch("https://api.coindesk.com/v1/bpi/historical/close.json?for=yesterday"); //el proyecto incluye info sobre las tasas del bitcoin en 3 fechas pasadas, pero para simplificar solamente estaré desplegando la tasa del día anterior
    const resPrecioAyer = await historicoPrecio.json();
    return {
        precioBitcoin: resPrecio.bpi.USD,
        precioBitcoinAyer: resPrecioAyer.bpi
    }
}

export default Index;